/* Overall stylesheet */
:root {
	--primary: #111;
	--secondary: #222;
	--accent: #f48;
	--hover: #289;
}

a, a:hover, a:focus, a:active {
	text-decoration: none;
	color: inherit;
}

.about {
	background-color: var(--secondary);
	border-radius: 20px;
	box-shadow: 10px 10px var(--accent);
	position: relative;
	padding: 2vw;
	text-align: left;
	max-width: 90vw;
	margin: auto auto 50px auto;
	
}

.about h1 {
	margin-top: 5px;
}

.App {
	
}

.art-card {
	background: url("img\\Surreal-myth.jpg") center repeat;
}

.art {
	border-radius: 10px;
	box-shadow: 10px 10px var(--secondary);
	margin: 7px;
	max-height: 200px;
	transition: transform 0.5s;
}

.art:active {
	box-shadow: none;
	position: fixed;
	top: 34%;
	transform: scale(3);
	max-width: 33vw;
	max-height: 30vh;
	z-index: 2;
}

body {
	background-color: var(--primary);
	color: white;
	text-align: center;
	font-family: "Arial Rounded MT", "Helvetica Rounded", Arial, sans-serif;
}

.card {
	width: 40vw;
	min-width: 20vw;
	margin: auto;
	transition: width 0.3s;
	text-shadow: 2px 2px DeepPink;
}

.card div {
	border-radius: 10px;
	font-size: 5vw;
	padding-top: 60vh;
	text-align: left;
	box-shadow: 10px 10px var(--accent);
}

.card:hover {
	width: 55vw;
}

.card a:visited {
	color: white;
}

.contact {
	align-items: center;
	background-color: var(--secondary);
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: auto;
	min-height: 100px;
	border-radius: 10px;
	box-shadow: 10px 10px var(--accent);
	transition: background-color 0.3s;
}

.contact:hover {
	background-color: var(--hover);
}

iframe {
	background: white;
	border-radius: 10px;
	box-shadow: 10px 10px var(--accent);
	margin: 10px;
	min-width: 610px;
	width: 60vw;
	height: 50vh;
	min-height: 250px;
}

.cs {
	background: url("img\\ksbd-cio-fanart.jpg") center repeat;
}

.flex-container {
	align-items: center;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: auto;
	width: 90vw;
}

.flex-container a {
	margin: 20px;
}

footer{
	bottom: 10px;
	color: dimgray;
	position: relative;
	left: 50%;
	margin-top: 20px;
    transform: translateX(-50%);
	z-index: -1;
}

.grid-container {
	position: relative;
	top: 10 px;
	display: grid;
	grid-template-columns: auto auto;
	grid-column-gap: 1vw;
	
}

h3{
	color: var(--accent);
}

#overlay {
	position: fixed; /* Sit on top of the page content */
	display: none; /* Hidden by default */
	width: 100%; /* Full width (cover the whole page) */
	height: 100%; /* Full height (cover the whole page) */
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,0.5); /* Black background with opacity */
	z-index: 5; /* Specify a stack order in case you're using a different order for other elements */
	cursor: pointer; /* Add a pointer on hover */
}

p {
	margin: 10px;
	text-align: justify;
}

.profile {
	border-radius: 10px;
	
	margin-left: 2vw;
	margin-bottom: 1vw;
	float: right;
	width: 30vw;
	height: 33.75vw;
	max-width: 400px;
	max-height: 457px;
	min-width: 200px;
	min-height: 226px;
}

.spacer {
	color: var(--accent);
	margin: 40px 0px;
}